<script>
import '@/utils/i18n.js'
import { errorHandler } from '@/utils/errorHandler.js'

export default {
    name: 'DeletePeriodicDialog',
    props: ['isDeleteDialogShow', 'templateName', 'deleting'],
    data () {
        return {
            i18n: {
                deleteTips: gettext('确认删除')
            }
        }
